.page {
  display: flex;
  flex-direction: column;
  align-items: center
}

.app-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 5vh;
  text-align: center;
  padding: 12px;
  box-sizing: border-box;
  resize:vertical;
  overflow:auto;
  width: 100%;
}